import { ActionTypes } from "../constant/ActionTypes";

const initialState = { 
    data: []
}

export const currentUser = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.SETUSER:
            return{
                ...state,
                user: action.payload,
                loggedIn: true
            }
        case ActionTypes.LOGOUT:
            return {
                ...state,
                user: {},
                loggedIn: false
            }    
    
        default:
            return state
    }
}