import React from 'react'
import { Link } from 'react-router-dom'
import InputField2 from './InputField2'

export default function Residential() {
    return (
        <div>
            <h2 className="category">Residential</h2>
            <div className="form-group">
                <label htmlFor="action" className="sidebar-text">Please Select:</label>
                <select id="action" className="form-control">
                    <option>-Select-</option>
                    <option value="Appartment">Appartment</option>
                    <option value="Flat">Flat</option>
                    <option value="House/villa/kothi">House/villa/kothi</option>
                    <option value="Studio Apartment">Studio Apartment</option>
                    <option value="Soho Appartment">Soho Appartment</option>
                    <option value="land/Plot">land/Plot</option>
                    <option value="Farm House">Farm House</option>
                </select>
            </div>
            <div className="row">
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="City" label="City" isRequired />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="Society Name" label="Society Name" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="Locality" label="Locality" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="Sub Locality" label="Sub Locality" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Ap/No." />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="######" label="Pin Code" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Size" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Bed Rooms" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Bathrooms" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Roof" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Size/Area" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Furnished Type" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Parking" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Availability" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Drop Pin Location" />
                </div>
                <div className="col-sm-12 col-md-6">
                    <InputField2 fieldType="text" placeholder="" label="Possesion Status" />
                </div>
            </div>
            <div className="equal-padding d-flex space-between">
                <Link to="/add-property" className="btn btn-default"><i className="fa fa-angle-left" /> Back</Link>
                <button type="submit" className="btn btn-primary">Submit</button>
            </div>
        </div>
    )
}
