import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom'
import { setLogout } from '../../features/action/Action';

export default function Header() {
    const currentUser = useSelector(state => state.currentUser)
    const dispatch = useDispatch()

    return (
        <div className="navbar navbar-main navbar-default navbar-fixed-top" role="navigation">
        <div className="container-fluid">
          <div className="navbar-header">
            <a href="#sidebar-menu" data-effect="st-effect-1" data-toggle="sidebar-menu" className="toggle pull-left visible-xs"><i className="fa fa-bars" /></a>
            <button type="button" className="navbar-toggle collapsed" data-toggle="collapse" data-target="#main-nav">
              <span className="sr-only">Toggle navigation</span>
              <span className="icon-bar" />
              <span className="icon-bar" />
              <span className="icon-bar" />
            </button>
            <a href="#sidebar-chat" data-toggle="sidebar-menu" data-effect="st-effect-1" className="toggle pull-right visible-xs "><i className="fa fa-comments" /></a>
            <Link className="navbar-brand navbar-brand-primary hidden-xs" to="/">ThemeKit</Link>
          </div>
          <div className="collapse navbar-collapse" id="main-nav">
            
            <ul className="nav navbar-nav navbar-user">
              {/* User */}
              <li className="dropdown">
                <a href="#" className="dropdown-toggle" data-toggle="dropdown">
                  <img src="https://cdn.iconscout.com/icon/free/png-256/gentleman-1659508-1410030.png" width={35} alt="Bill" className="img-circle" /> {currentUser.user.name} <span className="caret" />
                </a>
                <ul className="dropdown-menu" role="menu">
                    <li><Link to={`/profile/${currentUser.user.username}`}><i className="fa fa-user" /> Profile</Link></li>
                    <li><Link to="/resetPassword"><i className="fa fa-wrench" /> Reset Password</Link></li>
                    <li><Link to="/" onClick={() => dispatch(setLogout())}><i className="fa fa-sign-out" /> Logout</Link></li>
                </ul>
              </li>
            </ul>
            <form className="navbar-form margin-none navbar-left hidden-xs ">
              {/* Search */}
              <div className="search-1">
                <div className="input-group">
                  <span className="input-group-addon"><i className="icon-search" /></span>
                  <input type="text" className="form-control" placeholder="Search a friend" />
                </div>
              </div>
              <div className="pull-left visible-md visible-lg" data-toggle="tooltip" data-placement="bottom" title="A few Color Examples. Download includes CSS Files for all color examples & the tools to Generate any Color combination. This Color-Switcher is for previewing purposes only.">
                <ul className="skins">
                  <li><span data-file="app/app" data-skin="default" style={{background: '#16ae9f'}} /></li>
                  <li><span data-file="skin-orange" data-skin="orange" style={{background: '#e74c3c'}} /></li>
                  <li><span data-file="skin-blue" data-skin="blue" style={{background: '#4687ce'}} /></li>
                  <li><span data-file="skin-purple" data-skin="purple" style={{background: '#af86b9'}} /></li>
                  <li><span data-file="skin-brown" data-skin="brown" style={{background: '#c3a961'}} /></li>
                </ul>
              </div>
            </form>
          </div>
        </div>
      </div>
    )
}
