import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import Layout from './Layout'
//import { validator } from "validator";
import isStrongPassword  from "validator/es/lib/isStrongPassword";
import apis from '../api/user';

export default function ResetPassword() {
    const currentUser = useSelector(state => state.currentUser)
    const [username, setUsername] = useState(currentUser.user.username)
    const [oldPassword, setOldPassword] = useState('')
    const [newPassword, setNewPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')

    const [errormsg, setErrormsg] = useState('')
    const [successmsg, setSuccessmsg] = useState('')
    const [message, setMessage] = useState('')
    const [color, setColor] = useState('')
    
    const resetPassword = async(e) => {
        e.preventDefault()
        if(oldPassword === '' || newPassword === '' || confirmPassword === ''){
            setErrormsg('All are required fields.')
        }else if(newPassword !== confirmPassword){
            setErrormsg('New Password and Confirm Password dose not match.')
        }else{
            setErrormsg('')
            const payload = {username, oldPassword, newPassword}
            await apis.resetPassword(payload).then(res => {
                setSuccessmsg("Password updated successfully")
            }).catch(err => {
                if(err.response.status === 401){
                    setErrormsg("Incorrect old Password")
                }else if(err.response.status === 500){
                    setErrormsg("Error Occurred while updating the password")
                }
                
            })
        }
        
    }
    
    const validate = (value) => {
        if (isStrongPassword(value, {
          minLength: 8, minLowercase: 1,
          minUppercase: 1, minNumbers: 1, minSymbols: 1
        })) {
            setMessage('Is Strong Password')
            setColor("green")
        } else {
            setMessage('Is Not Strong Password')
            setColor("red")
        }
      }

    
    return (
        <Layout>
            <div className="row">
                <div className="col-md-10 col-lg-8 col-md-offset-1 col-lg-offset-2">
                    <h1 className="page-section-heading">Reset Password</h1>
                    <div className="panel panel-default">
                        <div className="panel-body">
                            {errormsg && <div className="alert alert-danger">
                                {errormsg}
                            </div>}
                            {successmsg && <div className="alert alert-success">
                                {successmsg}
                            </div>}
                            <div className="row">
                                <div className="col-md-12">
                                    <div className="form-group form-control-default">
                                        <label>Username</label>
                                        <input type="text" disabled value={username} onChange={e => setUsername(e.target.value)} class="form-control" />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12">
                                    <div className="form-group form-control-default">
                                        <label>Old Password</label>
                                        <input type="password" value={oldPassword} autoComplete="off" onChange={e => setOldPassword(e.target.value)} class="form-control" />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-md-12">
                                    <div className="form-group form-control-default">
                                        <label>New Password</label>
                                        <input type="password" value={newPassword} onKeyUp={e => validate(e.target.value)} onChange={e => setNewPassword(e.target.value)} class="form-control" />
                                        {message && <div style={{color: color}}>
                                            {message}
                                        </div>}
                                    </div>
                                </div>
                            </div>
                            
                            <div className="row">
                                <div className="col-md-12">
                                    <div className="form-group form-control-default">
                                        <label>Confirm Password</label>
                                        <input type="password" value={confirmPassword} onChange={e => setConfirmPassword(e.target.value)} class="form-control" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div> 
                    <div className="text-center">
                        <button className="btn btn-success" onClick={resetPassword}>Reset Password</button>     
                        <br/><br/>                
                    </div> 
                </div>  
            </div>
        </Layout>
    )
}
