import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import InputField from '../components/InputField'
import apis from '../api/user'
import logo from '../logo.png'

function SignUp() {
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [phoneNumber, setPhoneNumber] = useState('')

    const [message, setMessage] = useState('')
    const [messageCSS, setMessageCSS] = useState('')
    

    const register = async(e) =>{
        e.preventDefault()
        const payload = { name, email, phoneNumber }

        await apis.signUp(payload).then(
            () => {
                setMessage('User has been Sucessfully Created Password has sent at your phone no.')
                setMessageCSS('alert-success')
            }
        ).catch(err => {
          if(err.response.status === 409){
            setMessage("User already exist.")
            setMessageCSS('alert-danger')
          }else if(err.response.status === 500){
            setMessage("Email already exist. Please try again")
            setMessageCSS('alert-danger')
          }else if(err.response.status === 400){
            setMessage("Please fill all details")
            setMessageCSS('alert-danger')
          } 
        })
    }
    
    return (
            <div className="login">
              <div className="container-fluid">
                <div className="lock-container">
                  <h1>Create a new account</h1>
                  <div className="panel panel-default text-center">
                    <img src={logo} alt="BrokHut" />
                    <div className="panel-body">
                    {message && <div className={`alert ${messageCSS}`}>
                      {message}
                    </div>}
                      <form role="form">
                        <InputField fieldType="text" label="Full Name" value={name} onChange={e => setName(e.target.value)} />
                        <InputField fieldType="email" label="Email" value={email} onChange={e => setEmail(e.target.value)} />
                        <InputField fieldType="text" label="Phone Number" value={phoneNumber} onChange={e => setPhoneNumber(e.target.value)} />

                        
                        <div className="text-center">
                            <button className="btn btn-primary btn-block" onClick={register}>Create an Account</button><br/>
                            Have an acconut? <Link to="/">Login Now</Link>
                        </div>
                    </form>
                      
                    </div>
                  </div>
                </div>
              </div>
        </div>
    )
}

export default SignUp;
