import React from 'react'

export default function InputField2({fieldType, label, value, onChange, placeholder, isRequired, mandatory}) {
    return (
        <div className={`form-group form-control-default ${isRequired && 'required'}`}>
            <label>{label}</label>
            <input type={fieldType} className="form-control" value={value} placeholder={placeholder} onChange={onChange} />
        </div>
    )
}
