import React from "react";
import clsx from "clsx";
import { Link, NavLink } from "react-router-dom";

const MenuItem = ({ item: { title, items = [], href, icon } }) => {
  const [open, setExpanded] = React.useState();
  const hasSubmenu = items.length > 0;
  const renderSubmenu = open && hasSubmenu;
  const toggle = React.useCallback(() => setExpanded(!open), [open]);

  return (
    <li className={clsx("menu-item", { open, hasSubmenu })}>
      <Link className="menu-item-title" to={href} onClick={hasSubmenu ? toggle : null}>
      <i className={icon} /> <span>{title}</span>
      </Link>

      {renderSubmenu && <Menu items={items} />}
    </li>
  );
};

const Menu = ({ items }) => {
  return !items?.length ? null : (
    <ul className="sidebar-menu">
      {items.map(item => (
        <MenuItem key={item.title} item={item} />
      ))}
    </ul>
  );
};

export default Menu;
