import { ActionTypes } from "../constant/ActionTypes";

export const setUser = users => {
    return{
        type: ActionTypes.SETUSER,
        payload: users
    }
}

export const setLogout = () => {
    return {
        type: ActionTypes.LOGOUT
    }
}

export const setProfile = data => {
    return {
        type: ActionTypes.SETPROFILE,
        payload: data
    }
}