import React from 'react'
import Menu  from '../../utils/sidebar-menu'
import {sidebarConfg}  from '../../utils/sidebar-confg'

export default function Sidebar() {
    return (
      <div className="sidebar left sidebar-size-2 sidebar-offset-0 sidebar-visible-desktop sidebar-visible-mobile sidebar-skin-dark" id="sidebar-menu" data-type="collapse">
        <div data-scrollable>
          <ul className="sidebar-menu">
            
            <li className="category">Navigation</li>
            <Menu items={sidebarConfg} />
          </ul>
        </div>
      </div>
    )
}
