import apiURL from './apiURL'
//Logins
export const signUp = payload => apiURL.post(`/signup`, payload)
export const login = payload => apiURL.post(`/login`, payload)
//Manage Passwords
export const forgotPassword = payload => apiURL.post(`/forgot/password`, payload)
export const resetPassword = payload => apiURL.post(`/update/password`, payload)
//Profile
export const getProfile = profileid => apiURL.get(`/fetch/profile/${profileid}`)
export const updateProfile = payload => apiURL.post(`/update/profile`, payload)

const apis = {
    signUp,
    login,
    getProfile,
    forgotPassword,
    resetPassword,
    updateProfile
}

export default apis