import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import apis from '../api/user'
import InputField from '../components/InputField'
import logo from '../logo.png'

export default function ForgotPassword() {
    const [username, setUsername] = useState('')
    const [message, setMessage] = useState('')
    const [messageCSS, setMessageCSS] = useState('')

    const changePassword = async(e) => {
        e.preventDefault()
        const payload = { username }
        if(username === ''){
            setMessage('Username is mandatory field.')
            setMessageCSS('alert-danger')
        }else{
            await apis.forgotPassword(payload).then(res => {
                setMessage(res.data)
                setMessageCSS('alert-success')
            }).catch(err => {
                if(err.response.status === 500){
                    setMessage("Password reset Failed.")
                    setMessageCSS('alert-danger')
                }else{
                    setMessage(err)
                    setMessageCSS('alert-danger')
                }
            })
        }
    }
    return (
        <div className="login">
              <div className="container-fluid">
                <div className="lock-container">
                  <h1>Forgot Password</h1>
                  <div className="panel panel-default text-center">
                    <img src={logo} alt="BrokHut" />
                    <div className="panel-body">
                        {message && <div className={`alert ${messageCSS}`}>
                        {message}
                        </div>}
                      <form role="form">
                        <InputField fieldType="text" value={username} onChange={e => setUsername(e.target.value)} label="Mobile No." />
                      
                        <div className="text-center">
                            <button className="btn btn-primary btn-block" style={{marginBottom: "5px"}} onClick={changePassword}>Submit</button><br/>
                            Back to? <Link to="/">Login Now</Link>
                        </div>
                    </form>
                      
                    </div>
                  </div>
                </div>
              </div>
        </div>
    )
}
