import React, { useState } from 'react'
import { Link, useRouteMatch, Switch, Route, NavLink, useHistory } from 'react-router-dom'
import Layout from './Layout'
import Residential from '../components/Residential'
import Commericial from '../components/Commericial'
import Industrial from '../components/Industrial'
import Agriculture from '../components/Agriculture'
import Other from '../components/Other'

export default function AddProperty() {
    const [expanded, setExpanded] = useState(false)
    let { url } = useRouteMatch();
    let history = useHistory();

    const [status, setStatus] = useState(1)

    const handleExpandeClick =() => {
        setExpanded(!expanded)
    }

    const handelStatus = (status) =>{
        setStatus(status)
        history.push("/add-property")
    }

    return (
      <Layout>
          <div className="navbar navbar-default navbar-size-large navbar-static-top navbar-map-overlay" style={expanded ? {width: '100%'} : {width: '51%'}}>
            <div className="navbar-text"><i className="fa fa-fw fa-info" /> Click on the map to add markers</div>
            <Link onClick={handleExpandeClick} className="toggle pull-right active hidden-xs margin-none"><i className="fa fa-edit" /></Link>
        </div>
          <aside style={expanded ? {display: 'none'} : {display: 'block'}} className="sidebar right sidebar-size-xs-1 sidebar-size-lg-48pc sidebar-size-40pc sidebar-offset-0 sidebar-skin-white sidebar-visible-desktop">
            <div data-scrollable>
                <form>
                <div className="sidebar-block">
                    <h1 className="text-h3 sidebar-heading">Create listing</h1>
                </div>
                <div className="sidebar-block">
                    
                        <div className="form-group marginTop">
                        <label htmlFor="action" className="sidebar-text">I would like to:&nbsp;</label>
                        <div className="radio radio-info radio-inline">
                            <input type="radio" id="inlineRadio1" name="radioInline" checked={status === 1} onClick={e => handelStatus(1)} value="Sell" defaultChecked />
                            <label htmlFor="inlineRadio1">Sell</label>
                        </div>
                        <div className="radio radio-info radio-inline">
                            <input type="radio" id="inlineRadio2" name="radioInline" checked={status === 2} onClick={e => handelStatus(2)} value="Rent" />
                            <label htmlFor="inlineRadio2">Rent</label>
                        </div>
                    </div>

                    {status === 1 && 
                        <ul className="property-section">
                            <li><NavLink activeClassName="active1" to={`${url}/residential`}><i className="fa fa-plus" />  Residential</NavLink></li>
                            <li><NavLink activeClassName="active1" to={`${url}/commericial`}><i className="fa fa-plus" />  Commericial</NavLink></li>
                            <li><NavLink activeClassName="active1" to={`${url}/industrial`}><i className="fa fa-plus" />  Industrial</NavLink></li>
                            <li><NavLink activeClassName="active1" to={`${url}/agriculture`}><i className="fa fa-plus" />  Agriculture</NavLink></li>
                            <li><NavLink activeClassName="active1" to={`${url}/other`}><i className="fa fa-plus" /> Other</NavLink></li>
                        </ul>
                    }
                    {status === 2 && <div className="text-danger">No Data Found</div>}
                    
                    <Switch>
                        <Route path={`${url}/residential`} exact>
                            <Residential />
                        </Route>
                        <Route path={`${url}/commericial`}>
                            <Commericial />
                        </Route>
                        <Route path={`${url}/industrial`}>
                            <Industrial />
                        </Route>
                        <Route path={`${url}/agriculture`}>
                            <Agriculture />
                        </Route>
                        <Route path={`${url}/other`}>
                            <Other />
                        </Route>
                    </Switch>
                </div>
                </form>
            </div>
            </aside>
      </Layout>
    )
}
