import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import InputField from '../components/InputField'
import apis from '../api/user'
import { useDispatch } from 'react-redux'
import { setUser } from '../features/action/Action'
import logo from '../logo.png'

export default function Login() {
    const dispatch = useDispatch()

    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [message, setMessage] = useState('')
    const [messageCSS, setMessageCSS] = useState('')


    const signin = async(e) => {
        e.preventDefault()
        
        await apis.login({username, password}).then(auth => {
          if(auth.data === true){
            apis.getProfile(username).then(response => {
              dispatch(setUser({
                email: response.data.email,
                username: response.data.username,
                name: response.data.name,
                phoneNumber: response.data.phoneNumber,
                address: response.data.address,
                noOfPartners:response.data.noOfPartners,
              }))
            })
            
          }else if(auth.data === false){
            setMessage("Password dosen't match")
            setMessageCSS('alert-danger')
          }
        }).catch(err => {
          if(err.response.status === 401){
            setMessage("Username dosen't exist")
            setMessageCSS('alert-danger')
          }else if(err.response.status === 500){
            setMessage("Sign Up Failed. Please try again")
            setMessageCSS('alert-danger')
          }else if(err.response.status === 400){
            setMessage("Please fill all details")
            setMessageCSS('alert-danger')
          }
        })
    }
    
    return (
            <div className="login">
              <div className="container-fluid">
                <div className="lock-container">
                  <h1>Account Access</h1>
                  <div className="panel panel-default text-center">
                    <img src={logo} alt="BrokHut" />
                    <div className="panel-body">
                    {message && <div className="alert alert-danger">
                      {message}
                    </div>}
                      <form role="form">
                        <InputField fieldType="text" label="Username" value={username} onChange={e => setUsername(e.target.value)} />
                        <InputField fieldType="password" label="Password" value={password} onChange={e => setPassword(e.target.value)} />
                      
                        <div className="text-center">
                            <button className="btn btn-success btn-block" onClick={signin}>Login  <i className="fa fa-fw fa-unlock-alt" /></button><br/>
                            Don't have an acconut? <Link to="/register">Register Now</Link>
                        </div>

                        <Link to="/forgotPassword" className="forgot-pass">
                        Forgot your Password? 
                        <i className="fa fa-question-circle" />
                    </Link>
                    </form>
                      
                    </div>
                  </div>
                  <div className="text-center">
                  
                  </div>
                </div>
              </div>
        </div>
    )
}
