import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import apis from '../api/user'
import UploadComponent from '../components/upload'
import Layout from './Layout'


export default function Profile() {
    const currentUser = useSelector(state => state.currentUser)
    
    //Messages
    const [errormsg, setErrormsg] = useState('')
    const [successmsg, setSuccessmsg] = useState('')

    //Personal Details
    const [name, setName] = useState(currentUser.user.name)
    const [username, setUsername] = useState(currentUser.user.username)
    const [email, setEmail] = useState(currentUser.user.email)
    const [phoneNumber, setPhoneNumber] = useState(currentUser.user.phoneNumber)
    const [address, setAddress] = useState(currentUser.user.address)

    // Firm Type Dropdown change properties
    const [Proprieter, setProprieter] = useState(false)
    const [Partnership, setPartnership] = useState(false)
    const [PrivateLimited, setPrivateLimited] = useState(false)
    const [LLP, setLLP] = useState(false)
    const [OPC, setOPC] = useState(false)

    //add photos
    const [officePhotos, setOfficePhotos] = useState({
        pictures: [],
        buttonText:"Add Office Photos",
        maxFileSize: 5242880,
        imgExtension: [".jpg", ".png"],
        defaultImages: []
    })

    const [brokerPhoto, setBrokerPhoto] = useState({
        pictures: [],
        buttonText:"Add Broker Photo",
        maxFileSize: 5242880,
        imgExtension: [".jpg", ".png"],
        defaultImages: [],
        singleImage: true
    })
    const handleChange = files => {
        const { pictures } = officePhotos;
        console.warn({ pictures, files });
        setOfficePhotos(
          {
            ...officePhotos,
            upload: {
              ...officePhotos,
              pictures: [...pictures, ...files]
            }
          },
          () => {
            console.warn("It was added!");
          }
        );
      };
    const handleChangeBroker = files => {
        const { pictures } = brokerPhoto;
        console.warn({ pictures, files });
        setBrokerPhoto(
            {
            ...brokerPhoto,
            upload: {
                ...brokerPhoto,
                pictures: [...pictures, ...files]
            }
            },
            () => {
            console.warn("It was added!");
            }
        );
    };

      
    const confirmUpload = () => {
        const { pictures, defaultImages } = officePhotos;
        console.log("Confirm Upload =>", [...pictures]);
    }

    //update Profile API
    const handleSubmit = async(e) => {
        e.preventDefault()
        const payload = {username, name, phoneNumber, address}
        await apis.updateProfile(payload).then(res => {
            setSuccessmsg('Profile Updated Successfully.')
        }).catch(err => {
            if(err.response.status === 500){
                setErrormsg('Profile Update Failed')
            }
            setErrormsg(err + 'Profile Update Failed')
        })
    }

    const firmType = (event) => {
        if(event.target.value == 1){
            setProprieter(true)
            setPartnership(false)
            setPrivateLimited(false)
            setLLP(false)
            setOPC(false)
        }else if(event.target.value == 2){
            setProprieter(false)
            setPartnership(true)
            setPrivateLimited(false)
            setLLP(false)
            setOPC(false)
        }else if(event.target.value == 3){
            setProprieter(false)
            setPartnership(false)
            setPrivateLimited(true)
            setLLP(false)
            setOPC(false)
        }else if(event.target.value == 4){
            setProprieter(false)
            setPartnership(false)
            setPrivateLimited(false)
            setLLP(true)
            setOPC(false)
        }else if(event.target.value == 5){
            setProprieter(false)
            setPartnership(false)
            setPrivateLimited(false)
            setLLP(false)
            setOPC(true)
        }else if(event.target.value == 0){
            setProprieter(false)
            setPartnership(false)
            setPrivateLimited(false)
            setLLP(false)
            setOPC(false)
        }
    }

    const [profile, setProfile] = useState({
        file: '',
        imagePreviewUrl: 'https://cdn.iconscout.com/icon/free/png-256/gentleman-1659508-1410030.png',
    })

    const photoUpload = e =>{
        e.preventDefault();
        const reader = new FileReader();
        const file = e.target.files[0];
        reader.onloadend = () => {
        setProfile({
            file: file,
            imagePreviewUrl: reader.result
          });
        }
        reader.readAsDataURL(file);
      }
     
    return (
        <Layout>
            <div className="media media-grid media-clearfix-xs">
                {errormsg && <div className="alert alert-danger">
                  {errormsg}
                </div>}
                {successmsg && <div className="alert alert-success">
                  {successmsg}
                </div>}
                <div className="media-left">
                <div className="width-250 width-auto-xs">
                    <div className="panel panel-default widget-user-1 text-center">
                    <div className="avatar">
                        <label htmlFor="photo-upload" className="custom-file-upload fas">
                            <div className="img-wrap img-upload" >
                            <img for="photo-upload" src={profile.imagePreviewUrl}/>
                            </div>
                            <input id="photo-upload" onChange={photoUpload} accept="image/png, image/gif, image/jpeg" type="file" /> 
                        </label>
                        <h3>{name} <small>({username})</small></h3>
                    </div>
                    </div>
                    {/* Contact */}
                    <div className="panel panel-default">
                    <div className="panel-heading">
                        Contact
                    </div>
                    <ul className="icon-list icon-list-block">
                        <li><i className="fa fa-envelope fa-fw" /> <a href={`mailto:${email}`}>{email}</a></li>
                        <li><i className="fa fa-phone fa-fw" /> <Link>{phoneNumber}</Link></li>
                    </ul>
                    </div>
                </div>
                </div>
                <div className="media-body">
                <div className="row">
                    <div className="col-md-6">
                    <div className="panel panel-default">
                        <div className="panel-heading panel-heading-gray">
                            <i className="fa fa-fw fa-info-circle" /> Permanent Address
                        </div>
                        <div className="panel-body">
                        <ul className="list-unstyled profile-about margin-none">
                            <li className="padding-v-5">
                                <textarea className="form-control" value={address} onChange={e => setAddress(e.target.value)}></textarea>
                            </li>
                        </ul>
                        </div>
                    </div>
                    </div>
                    <div className="col-md-6">
                    <div className="panel panel-default">
                        <div className="panel-heading panel-heading-gray">
                        <i className="fa fa-photo" /> Photos
                        </div>
                        <div className="panel-body">
                            <UploadComponent
                                {...officePhotos}
                                handleChange={handleChange}
                            />
                        </div>
                    </div>
                    </div>
                </div>
                <div className="panel panel-default">
                    <div className="panel-heading panel-heading-gray">
                    <i className="fa fa-pencil" /> Firm Details
                    </div>
                    <div className="panel-body">
                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group form-control-default">
                                    <label>Firm Name</label>
                                    <input type="text" className="form-control" />
                                </div>
                            </div>
                            <div className="col-md-6">
                                <div className="form-group form-control-default">
                                    <label>Working Areas</label>
                                    <input type="text" className="form-control" />
                                </div>
                            </div>
                        </div>
                        <div className="form-group form-control-default">
                            <label>Firm Permanent Address</label>
                            <textarea className="form-control"></textarea>
                        </div>
                        <div className="row">
                            <div className="col-md-6">
                                <div className="form-group form-control-default">
                                    <label>Firm Type</label>
                                    <select className="form-control" onChange={firmType}>
                                        <option value="0">- Select -</option>
                                        <option value="1">Proprieter</option>
                                        <option value="2">Partnership</option>
                                        <option value="3">Private Limited</option>
                                        <option value="4">LLP</option>
                                        <option value="5">OPC</option>
                                    </select>
                                    
                                </div>
                            </div>
                            <div className="col-md-6">
                                {Proprieter && <div>
                                    <div className="form-group form-control-default">
                                        <label >Aadhar Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Aadhar Card Copy (Front and Back)</label>
                                        <div className="d-flex">Front: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                        <div className="d-flex">Back: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >PAN Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >PAN Card Copy</label>
                                        <input type="file" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >GST Certificate</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                </div>}

                                {Partnership && <div>
                                    <div className="form-group form-control-default">
                                        <label >No. of Partners</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >MD Aadhar Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Aadhar Card Copy (Front and Back)</label>
                                        <div className="d-flex">Front: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                        <div className="d-flex">Back: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Managing PAN Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >PAN Card Copy</label>
                                        <input type="file" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >GST Certificate</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                </div>}

                                {PrivateLimited && <div>
                                    <div className="form-group form-control-default">
                                        <label >MOA Copy</label>
                                        <input type="file" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >COI Copy</label>
                                        <input type="file" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >AOA Copy</label>
                                        <input type="file" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Number of Directors</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >All Director Aadhar Copy (Front and back)</label>
                                        <div className="d-flex">Front: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                        <div className="d-flex">Back: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Company PAN Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Company PAN Card Copy</label>
                                        <input type="file" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >GST Certificate</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >BR of MD</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >TIN Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                </div>}

                                {LLP && <div>
                                    <div className="form-group form-control-default">
                                        <label >Partner Aadhar Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Partner Aadhar Copy (Front and Back)</label>
                                        <div className="d-flex">Front: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                        <div className="d-flex">Back: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Partner PAN</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                </div>}

                                {OPC && <div>
                                    <div className="form-group form-control-default">
                                        <label >Director Aadhar Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Director Aadhar Copy (Front and Back)</label>
                                        <div className="d-flex">Front: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                        <div className="d-flex">Back: <input type="file" style={{marginLeft: "5px"}} className="form-control" /></div>
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Director PAN Number</label>
                                        <input type="text" className="form-control" />
                                    </div>
                                    <div className="form-group form-control-default">
                                        <label >Director PAN Card Copy</label>
                                        <input type="file" className="form-control" />
                                    </div>
                                </div>}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="panel panel-default">
                    <div className="panel-heading panel-heading-gray">
                        <i className="fa fa-location-arrow" /> Firm Google Location
                    </div>
                    <div className="panel-body">
                        
                    </div>
                </div>
                </div>
                <div className="pull-right">
                    <br/>
                    <button onClick={handleSubmit} className="btn btn-success">Update</button>     
                    <br/><br/>
                </div>
            </div>
        </Layout>
    )
}
